<template>
    <div class="payment">
        <form id="orderform" method="post" hidden>
			<input id="orderUrl" />
			providerId:<input type="text" id="providerId" name="providerId" />
			orderInfo:<input type="text" id="orderInfo" name="orderInfo" /> 
			returnUrl:<input type="text" id="returnUrl" name="returnUrl" /> 
			imageUrl:<input type="text" id="imageUrl" name="imageUrl" /> 
			mac:<input type="text" id="mac" name="mac" /> 
			deviceType:<input type="text" id="deviceType" name="deviceType" value="hd" /> 
			userGroup:<input type="text" id="userGroup" name="userGroup" value="client" /> 
			deviceSerialNo:<input type="text" id="deviceSerialNo" name="deviceSerialNo" value="" /> 
			deviceModel:<input type="text" id="deviceModel" name="deviceModel" value="HW" /> 
			notifyUrl:<input type="text" id="notifyUrl" name="notifyUrl" /> 
			contentId:<input type="text" id="contentId" name="contentId" /> 
			contentName:<input type="text" id="contentName" name="contentName" />
			<button type="button" id="saveOrder" class="bt-primary">提交</button>
		</form>
    </div>
</template>
<script>
import {client as yh} from '../../js/client.js'
export default {
    data(){
        return{
			payOb:null,
			tradeInfo:null,
			errorInfo:null,
			payBack:null,
        }
    },created(){
		this.payOb = this.$route.query.payOb;
		this.tradeInfo = this.$route.query.tradeInfo;
		this.errorInfo = this.$route.query.errorInfo;
		this.payBack = this.$route.query.payBack;
		console.log("回调中间页页面地址:"+location.href);
		if(this.tradeInfo){ // 如果此字段存在则说明支付成功继续播放  先判断是否有值最后才判断是否有返回参数
			console.log("支付结果："+this.tradeInfo);
			localStorage.setItem("userPayStatus","paySucess");
			yh.closePage();
        	return false;
		}
		if(this.errorInfo){  // 如果返回此字段说明支付失败继续试看
			console.log("支付失败："+ this.errorInfo);
			localStorage.setItem("userPayStatus","payFail");
			yh.closePage();
			return false;
		}
		if(this.payBack && this.tradeInfo==undefined){ // 支付页面返回结果
			console.log("支付页面返回用户未支付");
			localStorage.setItem("userPayStatus","noPay");
			yh.closePage();
			return false;
		}
	},mounted(){
		if(this.payOb){
			console.log("详情页请求支付")
			const payob = JSON.parse(this.payOb);
			this.dealPayOrder(payob);
		}
		this.$nextTick(function(){
			 $('.openImage').hide();
			 $('.loading').show();
		})
    },methods:{
        dealPayOrder(data) {
			console.log(JSON.stringify(data));
				if(data) {
					$("#providerId").val(data.providerId);
					$("#orderInfo").val(data.orderInfo);
					$("#returnUrl").val(data.returnUrl);
					$("#imageUrl").val(data.imageUrl);
					$("#notifyUrl").val(data.notifyUrl);
					$("#contentId").val(data.contentId);
					$("#contentName").val(data.contentName);
					$("#deviceType").val("hd");
					$('#mac').val(yh.mac);
					$("#orderUrl").val(data.orderUrl);
					$("#orderform").attr("action", data.orderUrl);
					//            $("#orderUrl").val("http://171.14.99.98:9001/hnitv-api/order");//福富测试环境
					//            $("#orderform").attr("action","http://171.14.99.98:9001/hnitv-api/order");//福富测试环境
					console.log("#####providerId:" + data.providerId);
					var orderUrl = $("#orderform").attr("action");
					console.log("表单提交地址:"+orderUrl);
					if(orderUrl != null && orderUrl.length > 0) {
						$("#orderform").submit();
						return;
					}
				}

			}

    }
}
</script>



