
<template>
    <div class="CooperationArea">
            <RecommendTop v-on:pressDown="keepDo"
                    v-on:eleScroll="changeHeight" ref="recommendTop" :initData="recommend" :catName="catName" :title="title" ></RecommendTop>
            <RecommendContent v-if="!isMore" v-on:pressDown="keepUpDo"
                    v-on:eleScroll="changeHeight" ref="recommendContent" :initData="recommend" :catName="catName" :title="title" ></RecommendContent>
            <recommend-bottom v-if="isMore" v-on:pressDown="keepUpDo"
                    v-on:eleScroll="changeHeight" ref="recommendBottom" :initData="recommend"  :catName="catName" :specialCode="specialCode" :title="title"></recommend-bottom>
    </div>
</template>
<style scoped>
</style>
<script>
import { HTTPUtil as api } from "../../fetch/api.js";
import RecommendTop from "../cooperative/Cooperative_Rec";
import RecommendContent from "../cooperative/Cooperative_ThreeAssets";
import RecommendBottom from "../cooperative/Cooperative_bottom";
export default {
  props: ["categoryData", "position", "specialCode", "title","catName"],
  name: "CooperativeMain",
  data() {
    return {
      recommend: [],
      isMore: false,
      isinit: false,
      zqCode: ""
    };
  },
  created() {
      //  if(!this.categoryData.jsonUrl)return;
      //  this.initData(this.categoryData.jsonUrl);
    console.log('Main create ' + this.specialCode);
  },
  activated() {
    console.log('Main activated ' + this.specialCode);
    if (this.categoryData != undefined) {
      if (!this.categoryData.jsonUrl) return;
      if (this.specialCode == undefined) {
        this.zqCode = localStorage.getItem("ZQCODE");
      } else {
        this.zqCode = this.specialCode;
        console.log('======================='+this.zqCode);
        localStorage.setItem("ZQCODE", this.specialCode);
      }
      this.initData(this.categoryData.jsonUrl);
    }
  },
  updated() {
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  beforeRouteEnter(to, from, next) {
    if (from.name != null) {
      localStorage.setItem("OpBack", from.name);
    }
    window.sessionStorage.removeItem('srcPagePath');
    window.sessionStorage.setItem('srcPagePath',location.origin+'/henan'+from.path);
    next();
  },
  methods: {
    initData(url) {
      api.get(url + "&returnType=jsonp").then(response => {
        if (response.code == 200) {
          this.recommend = response.data;
        }
      });
    },
    keepDo(show, nameId, pos) {
      if (this.position == 0) {
        if (pos == "down") {
          this.$refs.recommendContent.updateStast(true, pos, false, "专区");
        }
      } else {
        if (pos == "down") {
          this.$refs.recommendBottom.updateStast(true, pos, false, "专区");
        }
      }
      if (pos == "up") {
        this.$emit("navigation", nameId, pos, true);
      } else if (pos == "back") {
        this.$emit("navigation", nameId, pos, true);
      }
    },
    keepUpDo(show, nameId, pos) {
      if (pos == "up") {
        this.$refs.recommendTop.updateStast(true, pos, false);
      }
      if (pos == "back") {
        this.$emit("navigation", nameId, pos, true);
      }
    },
    changeHeight() {},
    updateStast(show, pos, isEnd) {
      this.$refs.recommendTop.updateStast(show, pos, isEnd);
    }
  },
  components: { RecommendTop, RecommendContent, RecommendBottom },
  watch: {
    categoryData() {
      if (this.categoryData != "" && this.categoryData != undefined) {
        this.initData(this.categoryData.jsonUrl);
      }
    },
    position() {
      if (this.position == 0) {
        this.isMore = false;
      } else {
        this.isMore = true;
      }
    },specialCode(){
      console.warn('main watch' + this.specialCode);
      if (this.specialCode == undefined || this.specialCode == "") {
        this.zqCode = localStorage.getItem("ZQCODE");
      } else {
          this.zqCode = this.specialCode;
          localStorage.setItem("ZQCODE", this.specialCode);
      }
    }
  }
};
</script>



