<template>
  <div class="cooperative3">
       <lazy-image v-for="(item,index) of elementList" :key="'telemcomB'+index" v-if="index>4&&index<8"
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass = "round"
                  />
        <div class="imglazy resultMore">
           <ul>
               <li></li>
               <li></li>
               <li></li>
               <li></li>
           </ul>
           <p>更多{{itemType}}</p>
        </div>
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
.cooperative3 {
  position: relative;
  width: 1190px;
  display: flex;
  height: 310px;
  margin-top: 40px;
}
.cooperative3 .imglazy {
  width: 265px;
  height: 137px;
  margin-right: 16px;
  border-radius: 15px;
}
.imglazy img {
  width: 100%;
  height: 100%;
}
.imglazy.hover {
  border: 3px solid #ffffff;
  transform: scale(1.1);
  margin: -3px 13px -3px -3px;
  border-radius: 18px;
  z-index: 99;
}
.resultMore {
  background-image: linear-gradient(180deg, #7ac2dd, #2578ac);
}
.resultMore ul {
  width: 140px;
  height: 15px;
  display: flex;
  justify-content: space-around;
  margin: 36px 0px 30px 60px;
}
.resultMore li {
  list-style: none;
  background-color: #ffffff;
  border-radius: 50%;
  height: 15px;
  width: 15px;
}
.resultMore p {
  text-align: center;
  font-size: 20px;
  font-family: Helvetica, "Regular";
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { xha } from "../../js/xha.js";
export default {
  name: "cooperative3",
  props: ["initData", "specialCode", "title","catName"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      typess: "",
      catCode: "",
      itemType: "",
      elementList: [
        {
          elementImg: ""
        },
        {
          elementImg: ""
        },
        {
          elementImg: ""
        }
      ],
      round: "round",
      ty: "",
      logName:'',
    };
  },
  created() {
    if (this.specialCode !== undefined) {
      this.ty = this.specialCode;
    }
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
    this.logName = this.title;
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        if (this.itemNo == 3) {
          try {
               let moreEntery = "更多"+this.catName;
               let pageTitle = this.title+'专区';
               let tj = "020"+(this.itemNo+1);
               xha.pageLoad(this.catName,pageTitle,moreEntery, '', tj, '');
          } catch (error) {
            
          }
          this.$router.push({
            name: "filter",
            params: {
              catCode: this.catCode,
              ty: this.ty,
              isVip: 1,
              title: this.title
            }
          });
          return;
        }
        const elementId = this.elementList[this.itemNo + 5].elementId;
        // 星红桉埋点
        let SrcPagePath =this.title+"专区";
        let PageName =  this.elementList[this.itemNo + 5].elementName;
        let AsssetID = elementId;
        let Id = "020"+(this.itemNo+1);
        let TYPE = "";
        xha.pageLoad(this.catName,SrcPagePath, PageName, AsssetID, Id, TYPE);
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        const eleDetail = this.elementList[this.itemNo + 5];
        c.routerSkip(
          eleDetail.jsonUrl,
          eleDetail.elementType,
          this.category[this.navpos],
          this.$router
        );
        window.localStorage.setItem('EnterType','2');
      }
    },
    back() {
      this.removeCss();
      this.itemNo = 0;
      this.pos = 0;
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo >= 3) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
    },
    down() {
      if (this.typess != "专区") {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    addCss() {
      if (this.pos == 0) {
        $(".cooperative3 .imglazy")
          .eq(this.itemNo)
          .addClass("hover");
      }
    },
    removeCss() {
      if (this.pos == 0) {
        $(".cooperative3 .imglazy")
          .eq(this.itemNo)
          .removeClass("hover");
      }
    },
    updateStast(bl, pos, isEnd, lie) {
      this.isShow = bl;
      this.typess = lie;
      this.addCss();
      if (bl) {
        let eleH = this.cEleOffsetTop(".cooperative3");
        this.$emit("eleScroll", this.name, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 310;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.eleId = this.initData.specialId;
      this.elementList = this.initData.elementList;
      this.itemType = this.initData.specialName;
      this.catCode = this.initData.specialCode;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  }
};
</script>



