<template>
    <div class="Zq_ThreeList">
        <div v-for="(item,index) in elementList" :key="'Zq_ThreeList'+index" v-if="index>4 && index<8" :class="('Zq_ThreeList_'+(index-5))">
            <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass='round'
                  />
        </div>
      <keyDo v-on:listenKeyCode="keyCode" v-show="false"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.Zq_ThreeList {
  position: relative;
  width: 1190px;
  height: 425px;
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
}
.Zq_ThreeList div {
  width: 355px;
  height: 202px;
  border: 1px solid #707070;
  border-radius: 15px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.Zq_ThreeList div div.hover {
  border: 3px solid #ffffff;
  margin: -2px 18px 18px -2px;
  transform: scale(1.07);
  border-radius: 18px;
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { xha } from "../../js/xha.js";
export default {
  name: "ZQThreeList",
  props: ["initData","title","catName"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      round: "round",
      typess: ""
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
  },
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const elementId = this.elementList[this.itemNo + 5].elementId;
        let SrcPagePath = this.title + "专区";
        let PageName = this.elementList[this.itemNo + 5].elementName;
        let AsssetID = elementId;
        let Id = "020"+(this.itemNo+1);
        let TYPE = "";
        xha.pageLoad(this.catName,SrcPagePath,PageName, AsssetID, Id, TYPE);
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        const obj = this.elementList[this.itemNo + 5];
        c.routerSkip(
          obj.jsonUrl,
          obj.elementType,
          this.category[this.navpos],
          this.$router
        );
        window.localStorage.setItem('EnterType','2');
      }
    },
    back() {
      this.removeCss();
      this.itemNo = 0;
      this.pos = 0;
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
      //      this.$emit('navigation',this.isShow,'back',true);
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo >= 2) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      if (this.itemNo > 2) {
        this.removeCss();
        this.itemNo -= 3;
        this.addCss();
        let eleH = this.cEleOffsetTop(".Zq_ThreeList_" + this.itemNo);
        this.$emit("eleScroll", this.eleId, "up", eleH);
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      }
    },
    down() {
      if (this.typess != "专区") {
        if (this.itemNo <= 2) {
          this.removeCss();
          this.itemNo += 3;
          this.addCss();
          let eleH = this.cEleOffsetTop(".Zq_ThreeList_" + this.itemNo);
          this.$emit("eleScroll", this.eleId, "down", eleH);
        } else {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "down");
        }
      }
    },
    addCss() {
      if (this.pos == 0) {
        $(".Zq_ThreeList_" + this.itemNo + " div").addClass("hover");
      }
    },
    removeCss() {
      if (this.pos == 0) {
        $(".Zq_ThreeList_" + this.itemNo + " div").removeClass("hover");
      }
    },
    updateStast(bl, pos, isEnd, lie) {
      this.isShow = bl;
      this.typess = lie;
      this.addCss();
      if (bl == true && isEnd == false) {
        let eleH = this.cEleOffsetTop(".Zq_ThreeList_" + this.itemNo);
        this.$emit("eleScroll", this.eleId, "down", eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = fEleDistance + 200;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.elementList = this.initData.elementList;
      this.eleId = this.initData.specialId;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  }
};
</script>



