<template>
  <div class="zqbox">
    <div class="scroll-box">
      <div id="sliderSegmentedControl"
           class="yh-scroll-wrapper yh-slider-indicator yh-segmented-control yh-segmented-control-inverted">
         <div class="cooperativeTitle">{{title}}</div>
         <div class="yh-scroll">
          <a v-for="(item,index) in navBar" ref='navItem' :class="{'yh-control-item':true,'yh-active':index===isActive,'yh-select':index===isSelect}" :catCode="item.catCode" :key="item.id" :id="'nav_'+index" >
            {{item.elementName}}
          </a>
        </div>
        <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
      </div>
        <router-view v-if="isload"  ref="childContent" v-on:navigation="IsNav" :categoryData="currentData" :position="itemNo" :title="title" :specialCode="specialCode" :catName="navBar[itemNo].elementName" ></router-view>
    </div>
  </div>
</template>
<style scoped>
  .zqbox {
    position: absolute;
    width: 1200px;
    padding-left: 80px;
    height: 695px;
    padding-top: 25px;
    overflow: hidden;
    background-color: #2D4158;
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }

  .scroll-box {
    position: relative;
  }
  .cooperativeTitle{
    font-size: 45px;
    opacity: 0.64;
    font-family:Helvetica, sans-serif;
    color: #FFFFFF;
    display: inline-block;
  }
  .yh-slider-indicator.yh-segmented-control {
    position: relative;
    bottom: auto;
    margin-left: -23px;
  }

  .yh-segmented-control.yh-scroll-wrapper {
    height: 90px;
    line-height: 90px;
  }
  .yh-segmented-control {
    font-size: 24px;
    font-weight: 400;
    position: relative;
    display: table;
    overflow: hidden;
    table-layout: fixed;
    border-radius: 3px;
    background-color: transparent;
  }

  .yh-scroll-wrapper {
    position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
  }
  .yh-segmented-control.yh-segmented-control-inverted {
    position: relative;
    overflow: hidden;
    border-radius: 0;
    padding-left: 22px;
    left:10px;
  }

  .yh-segmented-control.yh-scroll-wrapper .yh-scroll {
    height: 95px;
    white-space: nowrap;
    display:inline-block;
    line-height: 109px;
    padding-left: 54px;
  }
  .systembar{
    height: 39px;
    line-height: 39px;
    width: 360px;
    float:right;
  }
  .systembar li{
    list-style: none;
    float: left;
    padding: 0 10px;
    background-color: rgba(255,255,255,0.2);
    margin-right: 1px;
  }
  .systembar li.hover{
    background-color: #0098BC;
  }
  .systembar li:nth-child(1){
    border-radius: 9px 0px 0px 9px;
  }
  .systembar li:nth-child(3){
    border-radius: 0px 9px 9px 0px;
  }
  .yh-scroll {
    position: absolute;
    z-index: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }

  .yh-segmented-control.yh-segmented-control-inverted .yh-control-item {
    color: inherit;
  }
  .yh-segmented-control.yh-scroll-wrapper .yh-control-item {
    display: inline-block;
    width: auto;
    padding: 0 30px;
  }

  .yh-segmented-control .yh-control-item {
    line-height: 39px;
    display: table-cell;
    overflow: hidden;
    width: 1%;
    -webkit-transition: background-color .1s linear;
    transition: background-color .1s linear;
    text-align: center;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-decoration: none;
    border-radius: 5px;
    background: 0 0;
  }

  .yh-control-item.yh-active,systembar li.active {
    background-color: #0098BC;
    border:4px solid #FFF;
    margin: -3px;
    box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);
    border-radius: 15px;
  }

  .yh-control-item.yh-select {
    background: rgba(255, 255, 255, 0.431);
    border: 1px solid #ffffff;
     border-radius: 15px;
  }
  .select {
    background: rgba(255, 255, 255, 0.40);
    border-radius: 5px;
  }
  .time{
    font-size: 20px;
    float: right;
    margin-right: 60px;
    line-height: 60px;
    position: absolute;
    right: 50px;
    top: -55px;
    color: rgba(255,255,255,0.7);
  }
  .searchBtn{
    font-size: 30px;
  }


</style>
<script>
  import c from '../../js/common'
  import {mapGetters} from 'vuex'
  import {HTTPUtil as api} from '../../fetch/api.js'
  import {bp} from '../../js/ga.js'
  import {Authentication as auth} from '../../js/auth.js'
import { fail } from 'assert';
  export default {
    props:['jsonUrl'],
    name:'CooperativeNav',
    data: function () {
      return {
        itemNo: 0,
        sNo:-1,
        else:true,
        position: 0,
        count: 0,
        navBar: [{elementName:"推荐"}],
        isShow:true,
        currentData:{},
        isActive:0,
        isSelect:-1,
        keyPos:0,
        isOnce:false,
        title:'',
        isload:true,
        specialCode:'',
        bgImag:'',
      }
    }, created() {
      
    },
    activated(){
      console.log('url地址'+this.jsonUrl);
      if (!c.isAllNull(this.jsonUrl)) {
        this.isload = false;
        setTimeout(() => {
          this.isload = true;
        }, 0);
        this.itemNo =0;
        this.isActive=0;
        this.isSelect = -1;
        c.setLocal("cooperativeUrl",this.jsonUrl);
        this.currentData  = [];
        api.get(this.jsonUrl+"&returnType=jsonp").then((json) => {
          this.navBar = json.data.elementList;
          this.title = json.data.specialName;
          this.specialCode = json.data.specialCode;
          this.bgImag = json.data.specialImg;
          $('.zqbox').css('background-image','url('+this.bgImag+')');
          window.sessionStorage.setItem("zqLogCatName",this.title);
          let cData = this.filtrateRouter(this.itemNo);
          this.currentData = cData;
          this.isShow = true;
        });
        }
      this.ds();
    },created() {
      
    }, beforeRouteEnter (to, from, next) {
        if(from.name!=null){
          localStorage.setItem('detailback',from.name);
        }
        next();
    }, computed: {
      ...mapGetters([
        'rootPath',
        'catList',
        'category',
        'isHTTPS',
        'behindParams',
        'httpRequestTime',
        'httpResponseTime'
      ])
    },
    methods: {
      keyCode(kc) {
        console.log("专区接收键值");
        if (kc == "KeyBack") {
          this.title = '';
          this.isload = false;
          $('.zqbox').css("background-image","url()");
          this.$router.push({name:'index'});
        } else if (kc == 'left') {
          this.left();
        } else if (kc == 'right') {
          this.right();
        } else if (kc == 'down') {
          this.down();
        }else if(kc == 'KeyEnter') {
          console.log(this.position);
        }
      },left() {
          if(this.position==0){
           if(this.itemNo==0)return;
            this.itemNo--;
            if (this.itemNo < 0) {
                this.itemNo = 0;
                this.isActive = -1;
                $('.systembar ul li').removeClass('hover');
                this.position=1;
            }
            let toLeft = this.getElementH(this.itemNo);
            if(toLeft<50){
              if(this.itemNo!=0){
                $('.yh-scroll').animate({'left':'+=120px'});
              }
            }
        }
      }, right() {
        if(this.position==0){
          if(this.itemNo==(this.navBar.length-1))return;
          this.itemNo++;
          let toRight =  this.getElementH(this.itemNo);
          if(toRight>1170){
            $('.yh-scroll').animate({'left':'-=120px'});
          }
        }else if(this.position==1){
          this.isActive = this.itemNo;

        }
      }, down() {
        if (this.sNo <0 ){
          this.isSelect = this.itemNo;
        }
        this.isActive  = -1;
        $('.systembar ul li').removeClass('hover');
        this.isShow = false;
        this.$refs.childContent.updateStast(true, "down", false);
      },IsNav(name, pos, show){
        this.isShow = show;
        this.isSelect = -1;
        if(this.sNo==-1){
           this.isActive = this.itemNo;
        }
        $('.select').addClass('hover');
      },filtrateRouter(catCode){
        for(let i=0;i<this.navBar.length;i++){
            if(catCode == i){
               return this.navBar[i];
            }
        }
      },changeIsActive(index){
        if(this.position==0){
          this.isActive  = index;
          setTimeout(() => {
            this.currentData = this.filtrateRouter(this.itemNo);
          }, 300);
        }
      },
      ds() {
        setInterval(function () {
          var date = new Date();
          var h = date.getHours(); //获取小时
          var m = date.getMinutes(); //获取分钟
          if (h<10){
            $('.time').text('0' +h +':'+  m );
          }else {
            $('.time').text(h +':'+  m  );
          }
          if (m<10){
            $('.time').text(h + ':0' + m );
          }else {
            $('.time').text(h + ':' + m );
          }
        }, 1000)
      },getElementH(id){
        let fEleDistance = $('#nav_'+id).offset().left;
        return fEleDistance;
      },
      leavePage(){
         const pageType = 1;
         const pageId = this.navBar[this.itemNo].catId;
         const trace_type = 1;
         const trace_id = this.navBar[this.itemNo].catId;
         let keepTime = 0;
         if(this.isOnce){
            keepTime = new Date().getTime() - this.httpResponseTime;
         }else{
           keepTime = new Date().getTime() - this.httpRequestTime;
           this.isOnce = true;
         }
         const special_type = "";
         bp.closPage(pageId,pageType,trace_type, trace_id,special_type,keepTime);
      }
    },
    components: {

    },watch:{
        itemNo(){
          this.changeIsActive(this.itemNo);
        },specialCode(){
          console.warn('zq ' + this.specialCode);
        }
    }
  }
</script>



