<template>
    <div class="dailog">
        <auth-tip v-on:listernpopup="closeMsg" ref="auth"></auth-tip>
    </div>
</template>
<script>
import {Authentication as auth} from '../js/auth.js'
import AuthTip from '../page/firm.vue'
import {client as yh} from '../js/client.js'
export default {
    props:['msg','code'],
    created(){
         $('.openImage img').hide();
         $('.loading').hide();
    },mounted(){
        if(this.msg==4001){
             this.$refs.auth.warnLog("非法终端访问!请立即停止!");
        } else if(this.code===101){
            this.$refs.auth.warnLog("本地时间校验失败,请重启机顶盒!");
        }else {
            this.$refs.auth.warnLog("认证未通过，请重启机顶盒或联系客服MAC:"+yh.mac);
        }
    },methods:{
       closeMsg(isClose){
        if(isClose){
          localStorage.removeItem("Henan_Category");
          localStorage.removeItem("AuthResult");
          localStorage.removeItem("AAA");
          Toast("终端缓存清除成功，请重启客户端!");
          yh.back();
        }
      }
    },components:{
     AuthTip
   }
}
</script>



